}/* La timeline*/.timeline {
  position: relative;
  max-width: 1200px;
  margin: 0 auto;
}/* La propia timeline (línea vertical) */.timeline::after {
  content: '';
  position: absolute;
  width: 10px;
  background-color: #5a392b;
  top: 0;
  bottom: 0;
  left: 50%;
  margin-left: -3px;
}/* Contenido alrededor de la timeline */.container-timeline {
  padding: 10px 40px;
  position: relative;
  background-color: inherit;
  width: 50%;
}/* Círculos de la timeline */.container-timeline::after {
  content: '';
  position: absolute;
  width: 25px;
  height: 25px;
  right: -17px;
  background-color: white;
  border: 4px solid #5a392b;
  top: 15px;
  border-radius: 50%;
  z-index: 1;
}/* Situar los contenidos de la izquierda*/.left {
  left: 0;
}/* Situar los contenidos de la derecha */.right {
  left: 50%;
}/* Arregla la posición de los círculos del contenido de la derecha */.right::after {
  left: -16px;
}/* Añade flechas en el contenido de la izquierda apuntando a la derecha */.left::before {
  content: " ";
  height: 0;
  position: absolute;
  top: 22px;
  width: 0;
  z-index: 1;
  right: 30px;
  border: medium solid rgba(125, 190, 165, 0.36);
  border-width: 10px 0 10px 10px;
  border-color: transparent transparent transparent rgba(125, 190, 165, 0.36);
}/* Añade flechas en el contenido de la derecha apuntando hacia la izquierda. */.right::before {
  content: " ";
  height: 0;
  position: absolute;
  top: 22px;
  width: 0;
  z-index: 1;
  left: 30px;
  border: medium solid rgba(125, 190, 165, 0.36);
  border-width: 10px 10px 10px 0;
  border-color: transparent rgba(125, 190, 165, 0.36) transparent transparent;
}/* Estilo de las cajas con el contenido */.content-timeline {
  padding: 10px 30px;
  background-color: rgba(125, 190, 165, 0.36);
  position: relative;
  border-radius: 6px;
  font-size: 14px;
}

