    border-width: 10px 10px 10px 0;
    border-color: transparent rgba(125, 190, 165, 0.36) transparent transparent;
  }/* Para que todos los círculos estén alineados */  .left::after,.right::after {
    left: 15px;
  }

  .title-form {
    font-size: 18px;
  }

  .title-crea {
    margin-top: 15%;
    margin-left: 20px;
